import React from "react";

const MovieBox = props => {
  return (
    <div class="gif-results">
      <h2>You chose</h2>
      <h3 class="movie-title">Frozen</h3>
      <ul class="carousel" data-flickity='{"autoPlay":true}'>
        <li class="carousel-cell">
          <img
            class="carousel-cell-image"
            src="https://media.giphy.com/media/s4Bi420mMDRBK/giphy.gif"
            alt="gif"
          />
        </li>

        <li class="carousel-cell">
          <img
            class="carousel-cell-image"
            src="https://media.giphy.com/media/AUGqun7QALo0o/giphy.gif"
            alt="gif"
          />
        </li>

        <li class="carousel-cell">
          <img
            class="carousel-cell-image"
            src="https://media.giphy.com/media/e8YmiFHeDtfMs/giphy.gif"
            alt="gif"
          />
        </li>
      </ul>
    </div>
  );
};

export default MovieBox;
